</code></pre><div class="line-numbers"><span class="line-number">1</span><br><span class="line-number">2</span><br><span class="line-number">3</span><br><span class="line-number">4</span><br><span class="line-number">5</span><br></div></div><h3 id="获取客户朋友圈发表时选择的可见范围" tabindex="-1"><a class="header-anchor" href="#获取客户朋友圈发表时选择的可见范围" aria-hidden="true">#</a> 获取客户朋友圈发表时选择的可见范围</h3>
