</code></pre><div class="line-numbers"><span class="line-number">1</span><br><span class="line-number">2</span><br><span class="line-number">3</span><br><span class="line-number">4</span><br><span class="line-number">5</span><br><span class="line-number">6</span><br></div></div><h3 id="获取客户朋友圈的互动数据" tabindex="-1"><a class="header-anchor" href="#获取客户朋友圈的互动数据" aria-hidden="true">#</a> 获取客户朋友圈的互动数据</h3>
<div class="language-php ext-php line-numbers-mode"><pre v-pre class="language-php"><code><span class="token variable">$momentId</span> <span class="token operator">=</span> <span class="token string single-quoted-string">'momxxx'</span><span class="token punctuation">;</span>
