<blockquote>
<p>注意: 对于添加/删除/编辑企业客户标签接口，目前仅支持使用“客户联系”secret所获取的accesstoken来调用。
原文: https://work.weixin.qq.com/api/doc/90000/90135/92117</p>
</blockquote>
<h3 id="获取企业标签库" tabindex="-1"><a class="header-anchor" href="#获取企业标签库" aria-hidden="true">#</a> 获取企业标签库</h3>
<div class="language-php ext-php line-numbers-mode"><pre v-pre class="language-php"><code><span class="token variable">$tagIds</span> <span class="token operator">=</span> <span class="token punctuation">[</span>
    <span class="token string double-quoted-string">"etXXXXXXXXXX"</span><span class="token punctuation">,</span>
    <span class="token string double-quoted-string">"etYYYYYYYYYY"</span>
<span class="token punctuation">]</span><span class="token punctuation">;</span>

<span class="token variable">$app</span><span class="token operator">-></span><span class="token property">external_contact</span><span class="token operator">-></span><span class="token function">getCorpTags</span><span class="token punctuation">(</span><span class="token keyword type-hint">array</span> <span class="token variable">$tagIds</span><span class="token operator">=</span><span class="token punctuation">[</span><span class="token punctuation">]</span><span class="token punctuation">)</span><span class="token punctuation">;</span>
</code></pre><div class="line-numbers"><span class="line-number">1</span><br><span class="line-number">2</span><br><span class="line-number">3</span><br><span class="line-number">4</span><br><span class="line-number">5</span><br><span class="line-number">6</span><br></div></div><h3 id="添加企业客户标签" tabindex="-1"><a class="header-anchor" href="#添加企业客户标签" aria-hidden="true">#</a> 添加企业客户标签</h3>
<div class="language-php ext-php line-numbers-mode"><pre v-pre class="language-php"><code><span class="token variable">$params</span> <span class="token operator">=</span> <span class="token punctuation">[</span>
    <span class="token string double-quoted-string">"group_id"</span> <span class="token operator">=></span> <span class="token string double-quoted-string">"GROUP_ID"</span><span class="token punctuation">,</span>
    <span class="token string double-quoted-string">"group_name"</span> <span class="token operator">=></span> <span class="token string double-quoted-string">"GROUP_NAME"</span><span class="token punctuation">,</span>
    <span class="token string double-quoted-string">"order"</span> <span class="token operator">=></span> <span class="token number">1</span><span class="token punctuation">,</span>
    <span class="token string double-quoted-string">"tag"</span> <span class="token operator">=></span> <span class="token punctuation">[</span>
        <span class="token punctuation">[</span>
            <span class="token string double-quoted-string">"name"</span> <span class="token operator">=></span> <span class="token string double-quoted-string">"TAG_NAME_1"</span><span class="token punctuation">,</span>
            <span class="token string double-quoted-string">"order"</span> <span class="token operator">=></span> <span class="token number">1</span>
        <span class="token punctuation">]</span><span class="token punctuation">,</span>
        <span class="token punctuation">[</span>
            <span class="token string double-quoted-string">"name"</span> <span class="token operator">=></span> <span class="token string double-quoted-string">"TAG_NAME_2"</span><span class="token punctuation">,</span>
            <span class="token string double-quoted-string">"order"</span> <span class="token operator">=></span> <span class="token number">2</span>
        <span class="token punctuation">]</span>
    <span class="token punctuation">]</span>
<span class="token punctuation">]</span><span class="token punctuation">;</span>

<span class="token variable">$app</span><span class="token operator">-></span><span class="token property">external_contact</span><span class="token operator">-></span><span class="token function">addCorpTag</span><span class="token punctuation">(</span><span class="token keyword type-hint">array</span> <span class="token variable">$params</span><span class="token punctuation">)</span><span class="token punctuation">;</span>
</code></pre><div class="line-numbers"><span class="line-number">1</span><br><span class="line-number">2</span><br><span class="line-number">3</span><br><span class="line-number">4</span><br><span class="line-number">5</span><br><span class="line-number">6</span><br><span class="line-number">7</span><br><span class="line-number">8</span><br><span class="line-number">9</span><br><span class="line-number">10</span><br><span class="line-number">11</span><br><span class="line-number">12</span><br><span class="line-number">13</span><br><span class="line-number">14</span><br><span class="line-number">15</span><br><span class="line-number">16</span><br><span class="line-number">17</span><br></div></div><h3 id="编辑企业客户标签" tabindex="-1"><a class="header-anchor" href="#编辑企业客户标签" aria-hidden="true">#</a> 编辑企业客户标签</h3>
<div class="language-php ext-php line-numbers-mode"><pre v-pre class="language-php"><code><span class="token variable">$id</span> <span class="token operator">=</span> <span class="token string single-quoted-string">'TAG_ID'</span><span class="token punctuation">;</span>
<span class="token variable">$name</span> <span class="token operator">=</span> <span class="token string single-quoted-string">'NEW_TAG_NAME'</span><span class="token punctuation">;</span>
<span class="token variable">$order</span> <span class="token operator">=</span> <span class="token number">1</span><span class="token punctuation">;</span>

<span class="token variable">$app</span><span class="token operator">-></span><span class="token property">external_contact</span><span class="token operator">-></span><span class="token function">updateCorpTag</span><span class="token punctuation">(</span><span class="token keyword type-hint">string</span> <span class="token variable">$id</span><span class="token punctuation">,</span> <span class="token keyword type-hint">string</span> <span class="token variable">$name</span><span class="token punctuation">,</span> <span class="token keyword type-hint">int</span> <span class="token variable">$order</span> <span class="token operator">=</span> <span class="token number">1</span><span class="token punctuation">)</span><span class="token punctuation">;</span>
</code></pre><div class="line-numbers"><span class="line-number">1</span><br><span class="line-number">2</span><br><span class="line-number">3</span><br><span class="line-number">4</span><br><span class="line-number">5</span><br></div></div><h3 id="删除企业客户标签" tabindex="-1"><a class="header-anchor" href="#删除企业客户标签" aria-hidden="true">#</a> 删除企业客户标签</h3>
<div class="language-php ext-php line-numbers-mode"><pre v-pre class="language-php"><code><span class="token variable">$tagId</span> <span class="token operator">=</span> <span class="token punctuation">[</span>
    <span class="token string single-quoted-string">'TAG_ID_1'</span><span class="token punctuation">,</span>
    <span class="token string single-quoted-string">'TAG_ID_2'</span>
<span class="token punctuation">]</span><span class="token punctuation">;</span>
<span class="token variable">$groupId</span> <span class="token operator">=</span> <span class="token punctuation">[</span>
    <span class="token string single-quoted-string">'GROUP_ID_1'</span><span class="token punctuation">,</span>
    <span class="token string single-quoted-string">'GROUP_ID_2'</span>
<span class="token punctuation">]</span><span class="token punctuation">;</span>

<span class="token variable">$app</span><span class="token operator">-></span><span class="token property">external_contact</span><span class="token operator">-></span><span class="token function">deleteCorpTag</span><span class="token punctuation">(</span><span class="token keyword type-hint">array</span> <span class="token variable">$tagId</span><span class="token punctuation">,</span><span class="token keyword type-hint">array</span> <span class="token variable">$groupId</span><span class="token punctuation">)</span><span class="token punctuation">;</span>
</code></pre><div class="line-numbers"><span class="line-number">1</span><br><span class="line-number">2</span><br><span class="line-number">3</span><br><span class="line-number">4</span><br><span class="line-number">5</span><br><span class="line-number">6</span><br><span class="line-number">7</span><br><span class="line-number">8</span><br><span class="line-number">9</span><br><span class="line-number">10</span><br></div></div><h3 id="编辑客户企业标签" tabindex="-1"><a class="header-anchor" href="#编辑客户企业标签" aria-hidden="true">#</a> 编辑客户企业标签</h3>
<div class="language-php ext-php line-numbers-mode"><pre v-pre class="language-php"><code><span class="token variable">$params</span> <span class="token operator">=</span> <span class="token punctuation">[</span>
    <span class="token string double-quoted-string">"userid"</span> <span class="token operator">=></span> <span class="token string double-quoted-string">"zhangsan"</span><span class="token punctuation">,</span>
    <span class="token string double-quoted-string">"external_userid"</span> <span class="token operator">=></span> <span class="token string double-quoted-string">"woAJ2GCAAAd1NPGHKSD4wKmE8Aabj9AAA"</span><span class="token punctuation">,</span>
    <span class="token string double-quoted-string">"add_tag"</span> <span class="token operator">=></span> <span class="token punctuation">[</span><span class="token string double-quoted-string">"TAGID1"</span><span class="token punctuation">,</span> <span class="token string double-quoted-string">"TAGID2"</span><span class="token punctuation">]</span><span class="token punctuation">,</span>
    <span class="token string double-quoted-string">"remove_tag"</span> <span class="token operator">=></span> <span class="token punctuation">[</span><span class="token string double-quoted-string">"TAGID3"</span><span class="token punctuation">,</span> <span class="token string double-quoted-string">"TAGID4"</span><span class="token punctuation">]</span>
<span class="token punctuation">]</span><span class="token punctuation">;</span>

<span class="token variable">$app</span><span class="token operator">-></span><span class="token property">external_contact</span><span class="token operator">-></span><span class="token function">markTags</span><span class="token punctuation">(</span><span class="token keyword type-hint">array</span> <span class="token variable">$params</span><span class="token punctuation">)</span><span class="token punctuation">;</span>
</code></pre><div class="line-numbers"><span class="line-number">1</span><br><span class="line-number">2</span><br><span class="line-number">3</span><br><span class="line-number">4</span><br><span class="line-number">5</span><br><span class="line-number">6</span><br><span class="line-number">7</span><br><span class="line-number">8</span><br></div></div><h2 id="配置客户联系「联系我」方式" tabindex="-1"><a class="header-anchor" href="#配置客户联系「联系我」方式" aria-hidden="true">#</a> 配置客户联系「联系我」方式</h2>
<blockquote>
<p>{warning} 注意：</p>
<ol>
<li>通过API添加的「联系我」不会在管理端进行展示。</li>
<li>每个企业可通过API最多配置10万个「联系我」。</li>
<li>截止 2019-06-21 官方文档没有提供获取所有「联系我」列表的接口，请开发者注意自行保管处理 configId，避免无法溯源。</li>
</ol>
</blockquote>
<h3 id="增加「联系我」方式" tabindex="-1"><a class="header-anchor" href="#增加「联系我」方式" aria-hidden="true">#</a> 增加「联系我」方式</h3>
<div class="language-php ext-php line-numbers-mode"><pre v-pre class="language-php"><code><span class="token variable">$type</span> <span class="token operator">=</span> <span class="token number">1</span><span class="token punctuation">;</span>
<span class="token variable">$scene</span> <span class="token operator">=</span> <span class="token number">1</span><span class="token punctuation">;</span>
<span class="token variable">$config</span> <span class="token operator">=</span> <span class="token punctuation">[</span>
   <span class="token string single-quoted-string">'style'</span> <span class="token operator">=></span> <span class="token number">1</span><span class="token punctuation">,</span>
   <span class="token string single-quoted-string">'remark'</span> <span class="token operator">=></span> <span class="token string single-quoted-string">'渠道客户'</span><span class="token punctuation">,</span>
   <span class="token string single-quoted-string">'skip_verify'</span> <span class="token operator">=></span> <span class="token constant boolean">true</span><span class="token punctuation">,</span>
   <span class="token string single-quoted-string">'state'</span> <span class="token operator">=></span> <span class="token string single-quoted-string">'teststate'</span><span class="token punctuation">,</span>
   <span class="token string single-quoted-string">'user'</span> <span class="token operator">=></span> <span class="token punctuation">[</span><span class="token string single-quoted-string">'UserID1'</span><span class="token punctuation">,</span> <span class="token string single-quoted-string">'UserID2'</span><span class="token punctuation">,</span> <span class="token string single-quoted-string">'UserID3'</span><span class="token punctuation">]</span><span class="token punctuation">,</span>
<span class="token punctuation">]</span><span class="token punctuation">;</span>

<span class="token variable">$app</span><span class="token operator">-></span><span class="token property">contact_way</span><span class="token operator">-></span><span class="token function">create</span><span class="token punctuation">(</span><span class="token variable">$type</span><span class="token punctuation">,</span> <span class="token variable">$scene</span><span class="token punctuation">,</span> <span class="token variable">$config</span><span class="token punctuation">)</span><span class="token punctuation">;</span>

<span class="token comment">// {</span>
<span class="token comment">//   "errcode": 0,</span>
<span class="token comment">//   "errmsg": "ok",</span>
<span class="token comment">//   "config_id":"42b34949e138eb6e027c123cba77fad7"　　</span>
<span class="token comment">// }</span>
</code></pre><div class="line-numbers"><span class="line-number">1</span><br><span class="line-number">2</span><br><span class="line-number">3</span><br><span class="line-number">4</span><br><span class="line-number">5</span><br><span class="line-number">6</span><br><span class="line-number">7</span><br><span class="line-number">8</span><br><span class="line-number">9</span><br><span class="line-number">10</span><br><span class="line-number">11</span><br><span class="line-number">12</span><br><span class="line-number">13</span><br><span class="line-number">14</span><br><span class="line-number">15</span><br><span class="line-number">16</span><br><span class="line-number">17</span><br></div></div><h3 id="获取「联系我」方式" tabindex="-1"><a class="header-anchor" href="#获取「联系我」方式" aria-hidden="true">#</a> 获取「联系我」方式</h3>
<div class="language-php ext-php line-numbers-mode"><pre v-pre class="language-php"><code><span class="token variable">$configId</span> <span class="token operator">=</span> <span class="token string single-quoted-string">'42b34949e138eb6e027c123cba77fad7'</span><span class="token punctuation">;</span>

<span class="token variable">$app</span><span class="token operator">-></span><span class="token property">contact_way</span><span class="token operator">-></span><span class="token function">get</span><span class="token punctuation">(</span><span class="token variable">$configId</span><span class="token punctuation">)</span><span class="token punctuation">;</span>
</code></pre><div class="line-numbers"><span class="line-number">1</span><br><span class="line-number">2</span><br><span class="line-number">3</span><br></div></div><h3 id="更新「联系我」方式" tabindex="-1"><a class="header-anchor" href="#更新「联系我」方式" aria-hidden="true">#</a> 更新「联系我」方式</h3>
<div class="language-php ext-php line-numbers-mode"><pre v-pre class="language-php"><code><span class="token variable">$configId</span> <span class="token operator">=</span> <span class="token string single-quoted-string">'42b34949e138eb6e027c123cba77fad7'</span><span class="token punctuation">;</span>

<span class="token variable">$config</span> <span class="token operator">=</span> <span class="token punctuation">[</span>
   <span class="token string single-quoted-string">'style'</span> <span class="token operator">=></span> <span class="token number">1</span><span class="token punctuation">,</span>
   <span class="token string single-quoted-string">'remark'</span> <span class="token operator">=></span> <span class="token string single-quoted-string">'渠道客户2'</span><span class="token punctuation">,</span>
   <span class="token string single-quoted-string">'skip_verify'</span> <span class="token operator">=></span> <span class="token constant boolean">true</span><span class="token punctuation">,</span>
   <span class="token string single-quoted-string">'state'</span> <span class="token operator">=></span> <span class="token string single-quoted-string">'teststate2'</span><span class="token punctuation">,</span>
